import React, { ReactElement } from "react";
import Dadjoke from "./dadJoke";
import ProgrammingCard from "./programmingCard";

const About = (): ReactElement => {
    function calculateAge() {
        //  My Birth date
        const birthDate: Date = new Date(2001, 4, 7);
        const diff_ms = Date.now() - birthDate.getTime();
        const age_dt = new Date(diff_ms);

        return Math.abs(age_dt.getUTCFullYear() - 1970);
    }

    const LinearGradient = `linear-gradient(315deg, #ea8aba, #3f48cc)`;

    return (
        <div className="container container-about" id="About">
            <div className="about-information">
                <span
                    style={{
                        background: LinearGradient,
                    }}
                    className="colored-background"
                />
                <div className="content-card glass-box">
                    <h2>Oh this is the about page...</h2>
                    <h2>let me quickly tell something about myself.</h2>
                    <p className="glass-box">
                        I&apos;m Merlijn Sevenhuijsen a {calculateAge()} year
                        old person who likes a broad variety of different
                        things. Projects such as making music, making games and
                        a lot of other random topics. There&apos;s too little
                        time and too many interesting things to do in a day.
                        Finishing a project is the hardest part of starting one,
                        especially when you get stuck at a difficult part of a
                        project. I am a thinker, sometimes I even think too
                        much, but when it comes to projects I guess it is very
                        helpful! I like to search the whole internet for a
                        solution. Never fully content and always willing to try
                        and improve, which is at times very annoying, as it
                        requires me to re-make something multiple times, but
                        every time it gets better. When I started making this
                        website I wanted it to be a simple location to give
                        others insight into who I am, and maybe tell something
                        about projects on which I have worked. Besides that, it
                        is a learning experience, and I have re-designed the
                        whole website at least 4 times now. It is okay to fail
                        as long as you learn something from it. In my high
                        school years i have sometimes struggled with getting
                        passing grades, as some of the things I was learning did
                        not really fit my interests. This has, however, also
                        taught me that if you are willing to work hard for
                        something, you will achieve it.
                    </p>
                    <p className="glass-box">
                        I live in a small village in The Netherlands called
                        Dussen. There does not happen that much there, just a
                        few cows walking around and occasionally a sheep. My
                        Hobbies mostly revolve around computers, such as playing
                        video games as well as watching online content, like
                        everyone else nowadays. In my spare time I like to
                        listen to music as well, generally a broad range of
                        different music genres. When I was growing up, I would
                        play soccer with my friends at a local soccer club which
                        has taught me how to work together as a team. Currently,
                        I have been enjoying exploring nature on my bicycle. I
                        grew up with one brother who totally differs from my
                        personality, which can be annoying but also very handy
                        at times.
                    </p>
                </div>
            </div>
            <Dadjoke />
            <section className="content-about-programming">
                <ProgrammingCard
                    percentage={70}
                    programmingLanguage="Html"
                    color="rgb(255, 94, 0)"
                />
                <ProgrammingCard
                    percentage={60}
                    programmingLanguage="Css"
                    color="#f00"
                />
                <ProgrammingCard
                    percentage={90}
                    programmingLanguage="Javascript"
                    color="rgb(81, 255, 0)"
                />
                <ProgrammingCard
                    percentage={70}
                    programmingLanguage="Python"
                    color="rgb(0, 255, 157)"
                />
                <ProgrammingCard
                    percentage={80}
                    programmingLanguage="Java"
                    color="rgb(0, 38, 255)"
                />
                <ProgrammingCard
                    percentage={60}
                    programmingLanguage="C#"
                    color="rgb(140, 0, 255)"
                />
            </section>
            <div className="experiences">
                <div className="experience-title">
                    <h1>Past Experiences</h1>
                </div>
                <div className="experiences-box glass-box">
                    <h2> 2012 - 2019: High School </h2>
                    <p>
                        I finished VWO on Dongemond College in Raamsdonksveer,
                        The netherlands.
                    </p>
                </div>
                <div className="experiences-box glass-box">
                    <h2> 2019 - present: University </h2>
                    <p>
                        I am currently studying Computer Science and Engineering
                        at the TU in Eindhoven.
                    </p>
                </div>
            </div>
        </div>
    );
};

export default About;
